body {
	background-color: rgb(245, 245, 245);
}

.wrapper {
	max-width: 1280px;
	width: 90%;
	margin: 0 auto;
}

section {
	padding-bottom: 8%;
}

.container,
ul {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}

ul {
	list-style-type: none;
	padding: 0;
}

a {
	text-decoration: none;
}

a:hover,
button:hover {
	cursor: pointer;
}

.homeAnchor:hover::after {
	content: "";
	width: 15%;
	height: 0.25rem;
	background: $primaryFontColor;
	position: absolute;
	bottom: 20%;
}

/* Dropdown Menu for Mobile */

.dropdown {
	position: absolute;
	right: 0;
	top: 2rem;
}

.dropButton {
	position: relative;
	height: 3.3rem;
	border-top: 3px solid $primaryFontColor;
	border-bottom: 3px solid $primaryFontColor;
	border-left: 0;
	border-right: 0;
	font-weight: bold;
}

.dropdownContent {
	display: none;
	position: absolute;
	z-index: 1;
	padding-top: 1%;
	line-height: 3rem;
}

.dropdownContent a {
	text-decoration: none;
	border: 1px solid $primaryFontColor;
	display: block;
	text-align: center;
}

.dropButton,
.dropdownContent,
.dropdownContent a {
	width: clamp(6rem, 3rem + 10vw, 12rem);
}

.dropdownContent a:hover,
.dropdownContent a:focus {
	background-color: $primaryFontColor;
	color: white;
}

.dropdown:hover .dropdownContent,
.dropdown:focus .dropdownContent {
	display: block;
}

.dropdown:hover .dropButton,
.dropdown:focus .dropButton {
	background-color: $primaryFontColor;
	color: white;
}

footer {
	display: flex;
    flex-direction: column;
    align-items: center;
	justify-content: center;
	padding-bottom: 2%;

	img {
		width: 20%;
		box-shadow: 0 10px 50px 5px black;
        margin-bottom: 5%;
	}
}

@media screen and (min-width: 901px) {
	.dropdown {
		display: none;
	}
}

@media screen and (max-width: 900px) {
	.dropdown {
		display: inline-block;
	}

	.navContainer ul {
		display: none;
	}
}

@media screen and(max-width: 450px) {
	nav {
		margin-top: 2%;
	}
	.card {
		padding-bottom: 10%;
	}
}
