body {
	font-family: "Work Sans", sans-serif;
}

h1,
h2,
h3,
label,
footer {
	color: $primaryFontColor;
	font-weight: bold;
}

a {
	color: $primaryFontColor;
}

.mainHeading h1 {
	font-size: clamp(2.4rem, -1.3116rem + 8.8372vw, 10rem);
}

.portfolioHeading,
.contactHeading {
    font-size: clamp(3rem, 0.85714rem + 7.1428vw, 10rem);
}

h2 {
	font-size: 3.9rem;
}

h3,
p {
	font-size: 1.8rem;
}

.dropdownContent a {
    font-size: clamp(1.3rem, 1.05rem + 0.83333vw, 1.8rem);
}


nav {
	font-size: 2rem;
}

.mainHeading h3 {
	color: $secondaryFontColor;
}

.about h3 {
	font-size: 3.1rem;
}

.projects h3 {
	font-size: 2rem;
}

.fullTime {
	font-size: 1.6rem;
}

h4,
p {
	color: $secondaryFontColor;
}

h4 {
	font-weight: 600;
	font-size: 1.6rem;
}

input,
textarea,
label,
button {
	font-size: clamp(1.4rem, -0.43673rem + 6.12244vw,2rem);
	color: black;
}