.contactHeading {
	padding-bottom: 5%;
	padding-top: 0;
}

.contact {
	display: flex;
	justify-content: center;
	flex-direction: column;

	form {
		display: flex;
		flex-direction: column;

		label,
		input,
		textarea {
			margin-bottom: 1rem;
		}

		input {
			width: min(50%, 25rem);
		}

		textarea {
			width: min(90%, 60rem);
			resize: none;
		}

		button {
			width: min(20%, 8rem);
            padding: 1%;
		}

		button:hover,
		button:focus {
			background-color: $primaryFontColor;
			color: white;
			cursor: pointer;
		}
	}
}

/* The Modal (background) */
.modal {
	display: none;
	position: fixed;
	z-index: 1;
	padding-top: 100px;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	overflow: auto;
	background-color: $primaryFontColor;
	background-color: rgba(0, 0, 0, 0.4);
}

/* Modal Content */
.modalContent {
	background-color: #fefefe;
	position: relative;
	color: $primaryFontColor;
	margin: auto;
	padding: 20px;
	border: 1px solid #888;
	width: max(33%, 25rem);
}

/* The Close Button */
.close {
	color: #aaaaaa;
	position: absolute;
	right: 2%;
	top: 2%;
	font-size: 28px;
	font-weight: bold;
}

.close:hover,
.close:focus {
	color: #000;
	text-decoration: none;
	cursor: pointer;
}
