.portfolioHeading {
	padding-bottom: 10%;
}

.portfolioProjects {
	.card {
		margin-bottom: 5%;
	}
}

@media screen and (max-width: 800px) {
    .card {
        width: 45%;
    }
}

@media screen and (max-width: 550px) {
    .portfolioProjects {
        .container {
            margin-top: 5%;
            flex-direction: column;
            flex-wrap: nowrap;
            align-items: center;

            h4 {
                margin: 2.5% 0;
            }
        }

        .card {
            width: 80%;
            padding-bottom: 12.5%; 
        }
    }
}