header {
	padding-top: 5%;

	h1 {
		margin-bottom: 0;
	}

	nav {
		float: right;
		width: max(35%, 35rem);
		position: relative;

		ul a:hover::after {
			content: "";
			width: 20%;
			height: 0.25rem;
			background: $primaryFontColor;
			position: absolute;
			bottom: 20%;
		}
	}

	.mainHeading {
		padding-bottom: 5%;
	}

	.personalInfo {
		padding-bottom: 0;

		ul {
			width: 55%;
		}
	}

	.socialMedia ul {
		width: max(12%, 10rem);

		a {
			width: max(3%, 3rem);
		}

		a:hover,
		a:focus {
			outline: 2px solid $primaryFontColor;
			border-radius: 2.5%;
		}

		img {
			width: max(3%, 30px);
		}
	}
}
.about {
	display: flex;
	justify-content: space-evenly;
}

.coreCompetencies {
	li {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		flex-wrap: wrap;
		width: max(10%, 10rem);
	}

	img {
		display: flex;
		width: 30px;
	}

	img:hover {
		animation: spin 2s infinite;
		animation-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);

		@keyframes spin {
			0% {
				transform: rotate(0deg);
			}
			100% {
				transform: rotate(360deg);
			}
		}
	}
}

.card {
	display: flex;
	flex-direction: column;
	width: max(30%, 20rem);
}

.projects,
.portfolioProjects {
	.container {
		margin-bottom: 1%;
	}

	p {
		margin-bottom: 7.5%;
	}

	.stockOMeter,
	.quickFlickPix,
	.rainOrShine {
		position: relative;
		overflow-x: hidden;
	}

	.stockOMeter:hover::before {
		background-color: black;
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-image: url(../assets/dollar.jpg);
		filter: opacity(0.5);
		animation: moneyDrop 2s ease infinite;
	}

	@keyframes moneyDrop {
		0% {
			background-position: 20% 100%;
		}

		100% {
			background-position: 0 0;
		}
	}

	.quickFlickPix:hover::before {
		content: "";
		position: absolute;
		width: 329%;
		height: 100%;
		background-image: url(../assets/filmStrip.png);
		filter: opacity(0.3);
		animation: filmRoll 4s linear infinite;
	}

	@keyframes filmRoll {
		0% {
			transform: translateX(-100%);
		}

		100% {
			transform: translateX(100%);
		}
	}

	.rainOrShine:hover::before {
		background-color: black;
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-image: url(../assets/rain.png);
		filter: opacity(0.6);
		animation: rainAnimation 0.45s linear infinite;
	}

	@keyframes rainAnimation {
		0% {
			background-position: 20% 100%;
		}

		100% {
			background-position: 0 0;
		}
	}
}

.codeLinks {
	display: flex;
	justify-content: space-around;
	position: relative;

	a {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 37.5%;
	}

	a:hover,
	a:focus {
		outline: 2px solid $primaryFontColor;
		border-radius: 2.5%;
	}

	img {
		margin-right: 2%;
		padding: 2%;
		width: 2.5rem;
		height: 2.5rem;
	}

	img:hover {
		animation: spin 2s infinite;
		animation-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);

		@keyframes spin {
			0% {
				transform: rotate(0deg);
			}
			100% {
				transform: rotate(360deg);
			}
		}
	}
}

.portfolioLink {
	display: flex;
	position: relative;
	width: max(19%, 20rem);

	a:hover::after {
		content: "";
		width: 95%;
		height: 0.25rem;
		background: $primaryFontColor;
		position: absolute;
		bottom: 20%;
	}
}

.returnToTop {
	display: flex;
	justify-content: center;

	a {
		padding: 1%;
		border: 2px solid $primaryFontColor;
	}

	a:hover,
	a:focus {
		background-color: $primaryFontColor;
		color: white;
	}
}

@media screen and (max-width: 850px) {
    
    .mainHeading {
        padding-top: 7.5%;
    }
}